import ButtonBurgerMenu from "../../../IndexPage/sections/Header/ButtonBurgerMenu";
import './Header.scss';
import icons from "../../../../images/icons";

function Header() {
    return (
        <header>
            <section id="icon">
                <ButtonBurgerMenu/>
                <img src={icons['logo']} className="logo" alt="logo"/>
                <div className="search">
                    <img src={icons['search']} alt="search"/>
                    <input type="text"/>
                </div>
                <div className="social-network">
                    <a href="#"><img src={icons['instagram']} alt="instagram"/></a>
                    <a href="#"><img src={icons['viber']} alt="viber"/></a>
                    <a href="#"><img src={icons['whatsapp']} alt="whatsapp"/></a>
                    <a href="#"><img src={icons['facebook']} alt="facebook"/></a>
                </div>
                <a href="#" className="delivery">Доставка 25 р.</a>
                <a href="tel:+37355532805" className="phone-number"><img src={icons['phone']} alt="phone"/>+373 (555) 32805</a>
                <div className="classes">
                    <a href="#">Суши</a>
                    <a href="#">Сеты</a>
                    <a href="#">Салаты</a>
                    <a href="#">Супы</a>
                    <a href="#">Тайская еда</a>
                    <a href="#">Десерты</a>
                    <a href="#">Напитки</a>
                </div>
                <div className="empty"/>
            </section>
        </header>
)
}

export default Header;