import './Rev.scss';
import "./Stars/Stars";
import Card from "./Card/Card";


function Rev() {
    return (
        <section id="Rev">
            <h1>Отзывы</h1>
            <div className="container">
                <Card id='mw1'/>
                <Card id='mw576'/>
                <Card id='mw768'/>
                <Card id='mw1200'/>
            </div>
            <div className="points">
                <div className="point active"/>
                <div className="point passive"/>
                <div className="point active"/>
            </div>
            <p>Ждем Ваших заказов!</p>
        </section>
    )
}

export default Rev;