import './MainPopularCategories.scss';
// import icons from "../../../../../images/icons/icons";
import imgSets from '../../../../../images/sets.jpg';
import imgRolls from '../../../../../images/rolls.jpg';
import imgSups from '../../../../../images/sups.jpg';
import imgSushi from '../../../../../images/sushi.jpg';
import imgThaiFood from '../../../../../images/thai-food.jpg';


// function importAll(r) {
//     return r.keys().map(r);
// }
//
// const images = importAll(require.context('./', false, /\.(png|jpe?g|svg)$/));




function MainPopularCategories() {
    return (
        <section id="MainPopularCategories">
            <h1>Популярные категории</h1>
            <div className="cards">
                <div className="card">
                    <img src={imgSets} alt="сеты"/>
                    <div>
                        <p>Сеты</p>
                    </div>
                </div>
                <div className="mw576 card">
                    <img src={imgRolls} alt="роллы"/>
                    <div>
                        <p>Роллы</p>
                    </div>
                </div>
                <div className="card">
                    <img src={imgSups} alt="супы"/>
                    <div>
                        <p>Супы</p>
                    </div>
                </div>
                <div className="card">
                    <img src={imgSushi} alt="суши"/>
                    <div>
                        <p>Суши</p>
                    </div>
                </div>
                <div className="mw450 card">
                    <img src={imgThaiFood} alt="тайская еда"/>
                    <div>
                        <p>Тайская еда</p>
                    </div>
                </div>
            </div>
            <div className="points">
                <div className="point active"/>
                <div className="point passive"/>
                <div className="point active"/>
            </div>
        </section>
    )
}

export default MainPopularCategories;