import './WeOffer.scss';
import maps from './Maps';

function WeOffer() {
    return (
        <section id="WeOffer">
            <h1>Мы предлагаем</h1>
            <div className="container">
                <div className="card sushimaki">
                    <img src={maps['sushiMaki']} alt="сушимаки"/>
                    <div className="text">
                        <p>Суши Маки</p>
                    </div>
                </div>
                <div className="card sushiset">
                    <img src={maps['sushiSet']} alt="сушисет"/>
                    <div className="text">
                        <p>Суши Сет</p>
                    </div>
                </div>
                <div className="card nigiri">
                    <img src={maps['nigiri']} alt="нигири"/>
                    <div className="text">
                        <p>Нигири</p>
                    </div>
                </div>
            </div>
        </section>
    )
}

export default WeOffer;