import './Card.scss';
import Stars from "../Stars/Stars";

function Card(props) {
    return (
        <div className={`card ${props.id}`}>
            <div className="review">
                <p className="name">Надежда Р.</p>
                <br/>
                <p className="text">
                    Заказывали впервые в день рождения, гости остались довольны. Спасибо
                    большое!...
                </p>
            </div>
            <Stars />
        </div>

    )
}

export default Card;